.movie-card {
  background: #1a1a1a;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
  color: white;
}

.movie-card:hover {
  transform: translateY(-5px);
}

.poster-container {
  position: relative;
  aspect-ratio: 2/3;
}

.movie-poster {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-bottom: 3px solid #e50914;
}

.rating-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(0, 0, 0, 0.8);
  padding: 6px 12px;
  border-radius: 20px;
  font-weight: bold;
  backdrop-filter: blur(4px);
}

.movie-content {
  padding: 1.2rem;
}

.movie-title {
  margin: 0 0 0.8rem 0;
  font-size: 1.3rem;
  line-height: 1.3;
  min-height: 3.2rem;
}

.metadata-row {
  display: flex;
  gap: 0.8rem;
  color: #cccccc;
  font-size: 0.9rem;
  margin-bottom: 0.8rem;
}

.genre-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.genre-tag {
  background: #e50914;
  padding: 4px 10px;
  border-radius: 15px;
  font-size: 0.8rem;
  font-weight: 500;
}

.details-button-container {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.details-button {
  background: #e50914;
  padding: 6px 14px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  color: white;
  cursor: pointer;
  transition: background 0.3s ease;
  text-decoration: none;
  border: none;
}

.details-button:hover {
  background: #b20710;
}

.card-stats-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.8rem;
  margin: 1rem 0;
}

.card-stat-item {
  background: rgba(255, 255, 255, 0.05);
  padding: 0.8rem;
  border-radius: 8px;
  text-align: center;
}

.card-stat-label {
  display: block;
  font-size: 0.8rem;
  color: #999;
  margin-bottom: 0.3rem;
}

.stat-value {
  font-weight: bold;
  font-size: 1.1rem;
}

.movie-overview {
  color: #cccccc;
  font-size: 0.9rem;
  line-height: 1.4;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin: 0;
}

.no-poster {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #222;
  color: #888;
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  border-radius: 8px;
}


@media (max-width: 768px) {
  .movie-card {
    max-width: 100%;
  }
  
  .card-stats-grid {
    grid-template-columns: 1fr;
  }
}

.movie-card-small {
  transform: scale(0.85);
  max-width: 240px;
}

.movie-card-small .movie-title {
  font-size: 1rem;
  min-height: 2.2rem;
}

.movie-card-small .movie-content {
  padding: 0.8rem;
}

.movie-card-small .metadata-row {
  font-size: 0.75rem;
  gap: 0.5rem;
}

.movie-card-small .genre-tag {
  font-size: 0.7rem;
  padding: 2px 8px;
}

.movie-card-small .card-stats-grid {
  display: none; /* optional: hide stats to simplify */
}

.movie-card-small .movie-overview {
  display: none; /* optional: hide for smaller card */
}

.movie-card-small .details-button-container {
  margin-top: 0.5rem;
}

.movie-card-small .details-button {
  font-size: 0.75rem;
  padding: 4px 10px;
}
